// variables
$primaryColor: #f9a325;
$successColor: #25ad13;
$errorColor: #f15c59;
$textColor: #35405a;
$fontBold: Metropolis-Bold;
$noteColor: #666666;
$bgColorPrimary: #eeeeee;

// components
.primary-color{
  color: $primaryColor;
}

.success-color{
  color: $successColor;
}

.bg-color{
  background-color: $bgColorPrimary;
}

.font-bold{
  font-family: Metropolis-Bold;
}

.font-regular{
  font-family: Metropolis-Bold;
}

.title-text {
  color: $textColor;
  font-size: 36px;
  font-family: $fontBold;
  font-weight: bold;
  line-height: 42px;
}
.subtitle-text {
  color: $textColor;
  font-size: 24px;
  font-family: $fontBold;
  font-weight: bold;
  line-height: 32px;
}
.subtext-text {
  color: $textColor;
  font-size: 18px;
  line-height: 24px;
}

.note-text {
  color: $noteColor;
  font-size: 14px;
  line-height: 24px;
}

.error-text {
  color: $errorColor;
  font-size: 12px;
  line-height: 24px;
}

a{
  color: $textColor;
  &:hover{
    text-decoration: none;
    color: $textColor;
  }
}

.err-form{
  color: $errorColor;
}

.shadow{
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
}

.orange-btn{
  margin: 4px;
  color: white;
  background-color: #f9a325;
  padding: 9px 32px;
  border-radius: 30px;
  border: solid 1px #c6cbda;
  font-weight: bold;
}
.light-btn{
  margin: 4px;
  color: #35405a;
  background-color: white;
  padding: 9px 32px;
  border-radius: 30px;
  border: solid 1px #c6cbda;
}

input{
  font-family: FontAwesome;
}

.footer{
  position: absolute;
  bottom: 0;
}
$base-color: #ced4da;
$light-background: lighten(desaturate($base-color, 50%), 12.5%);
ul.pagination {
  margin-top: 0;
  margin-bottom: 0;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);

  li.page-item.active {
    a.page-link {
      color: saturate(darken($base-color, 50%), 5%) !important;
      background-color: saturate(lighten($base-color, 7.5%), 2.5%) !important;
      border-color: $base-color !important;
    }
  }

  a.page-link {
    padding: 0.5rem 0.5rem;
    min-width: 3.5rem;
    text-align: center;
    box-shadow: none !important;
    border-color: $base-color !important;
    color: saturate(darken($base-color, 30%), 10%);
    font-weight: 500;
    font-size: 1rem;

    &:hover {
      background-color: $light-background;
    }
  }
}

.modal-backdrop{
  z-index: 0;
}

.avatar-form {
  display: flex;
  .avatarnya {
    height: 88px;
    width: 88px;
    img {
      // background-image: url("../img/icons/user.png");
      background-size: 88px;
      // border-radius: 50%;
      position: absolute;
      width: 88px;
      height: 88px;
    }
  }
  .label-avatar {
    margin-left: 16px;
    display: grid;
  }
  .upload-photos {
    width: 107px;
    height: 48px;
    border-radius: 6px;
    box-shadow: 1px 2px 8px 0 rgba(0, 0, 0, 0.05);
    border: solid 1px #c6cbda;
    text-align: center;
    align-items: center;
    display: grid;
  }
}

@media only screen and (min-width: 0px) {
  .component-center {
    position: absolute;
    top: 0%;
    left: 0%;
    bottom: auto;
    transform: translate(0%,0%);
  }
  .checking{
    div{
      color:  #f9a325;
      text-decoration: underline;
    }
  }
}
@media only screen and (min-width: 480px) {
  .component-center {
    left: 50%;
    transform: translate(-50%,0%);
  }
}
@media only screen and (min-width: 768px) {
  .component-center {
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  .checking{
    text-align: left;
    div{
      color:  #f9a325;
      float: right;
      text-decoration: underline;
    }
  }
}
@media only screen and (min-width: 1024px) {

}